import React from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import PropTypes from "prop-types";
import {
  Tabs,
  Tab,
  Typography,
  Box,
  ListItem,
  ListItemIcon,
  List
} from "@material-ui/core";
import {
  ContactMail,
  Home,
  AccountBoxRounded,
  Work,
  LinkedIn,
  GitHub,
  Instagram,
  Facebook,
  Twitter
} from "@material-ui/icons";
import HomePage from "../pages/HomePage";
import AboutMe from "../pages/AboutMe";
const useStyles = makeStyles(theme => ({
  topIcon: {
    color: "#505050",
    textAlign: "center",
    marginTop: "0.5em",
    marginBottom: "30vh"
  },
  socialIcons: {
    color: "#505050",
    marginLeft: "2em"
  },
  root: {
    flexGrow: 1,
    backgroundColor: "#000",
    display: "flex",
    height: "100vh"
  }
}));

function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    "aria-controls": `vertical-tabpanel-${index}`
  };
}

export default function SideNav() {
  const classes = useStyles();
  const [value, setValue] = React.useState(1);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <StyledTabs
        textColor="secondary"
        indicatorColor="secondary"
        orientation="vertical"
        variant="standard"
        value={value}
        onChange={handleChange}
        aria-label="side nav"
        className={classes.tabs}
      >
        {/* Logo */}
        <Typography component="div" className={classes.topIcon} variant="h5">
          <Box fontWeight="fontWeightBold">V</Box>
        </Typography>

        {/* Tabs */}
        <StyledTab
          classes={{ root: classes.tab }}
          icon={<Home />}
          label="Home"
          {...a11yProps(0)}
        />
        <StyledTab
          icon={<AccountBoxRounded />}
          label="About me"
          {...a11yProps(1)}
        />
        <StyledTab icon={<Work />} {...a11yProps(2)} label="Projects" />
        <StyledTab icon={<ContactMail />} {...a11yProps(3)} label="Contact" />

        {/* Social Icons */}
        <List style={{ marginTop: "13vh" }}>
          <ListItem button>
            <ListItemIcon>
              <GitHub className={classes.socialIcons} />
            </ListItemIcon>
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <LinkedIn className={classes.socialIcons} />
            </ListItemIcon>
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <Facebook className={classes.socialIcons} />
            </ListItemIcon>
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <Instagram className={classes.socialIcons} />
            </ListItemIcon>
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <Twitter className={classes.socialIcons} />
            </ListItemIcon>
          </ListItem>
        </List>
      </StyledTabs>

      <TabPanel value={value} index={1}>
        <HomePage />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <AboutMe />
      </TabPanel>
      <TabPanel value={value} index={3}>
        Item Three
      </TabPanel>
      <TabPanel value={value} index={4}>
        Item Four
      </TabPanel>
    </div>
  );
}

const StyledTabs = withStyles(theme => ({
  root: {
    borderRight: `1px solid ${theme.palette.divider}`,
    backgroundColor: "#1c1c1c"
  },
  indicator: {
    backgroundColor: "#5E2BED"
  }
}))(Tabs);

const StyledTab = withStyles(theme => ({
  root: {
    color: "#505050",
    "&:hover": {
      color: "#fff",
      backgroundColor: "#424242"
    },
    "&$selected": {
      color: "#fff",
      fontWeight: theme.typography.fontWeightMedium
    }
  },
  selected: {}
}))(Tab);
